import { useEffect, useState } from "react";
import { TaskProjectManageService } from "../../../services/taskService";
import { Box, MyBox } from "../../core/MyBox";
import ProjectReportComponent from "./report/ProjectReportComponent";
import TaskReportComponent from "./report/TaskReportComponent";
import ToDoListReportComponent from "./report/ToDoListReportComponent";
import WorkflowReportComponent from "./report/WorkflowReportComponent";

export default function TaskIndexComponent() {
    const [projectClosed, setProjectClosed] = useState([]);
    const [projectNew, setProjectNew] = useState([]);
    const [projectOpen, setProjectOpen] = useState([]);
    const [toDoListDontWorks, setToDoListDontWorks] = useState([]);

    useEffect(() => {
        TaskProjectManageService.GetStatistic()
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setProjectNew(rs.data.projectNew);
                    setProjectOpen(rs.data.projectOpen);
                    setProjectClosed(rs.data.projectClosed);
                    setToDoListDontWorks(rs.data.toDoListDontWorks);
                }
            })
    }, [])

    return (
        <MyBox title="Tool quản lý công việc">
            <Box>
                <TaskReportComponent />
            </Box>

            <Box>
                <ProjectReportComponent
                    projectClosed={projectClosed}
                    projectNew={projectNew}
                    projectOpen={projectOpen} />
            </Box>

            <Box>
                <ToDoListReportComponent
                    data={toDoListDontWorks} />
            </Box>
            <Box>
                <WorkflowReportComponent />
            </Box>

            <Box>
                <h3>Danh sách các thông báo task</h3>
            </Box>
        </MyBox>
    );
}