import { Col, Row } from "antd";
import { Link } from "react-router-dom";
import { Box } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import { Text, TextStrong } from "../../../core/Text";
import { UIFormatDateTimeVNComponent } from "../../../core/UI_FormatComponent";

export default function ToDoListReportComponent(props: any) {
    const { data } = props;

    function RenderToDoListDontWork() {
        return data.map((data: any, index: number) => {
            return (
                <Box key={index}>
                    <Text>{data.content}</Text>
                    <TextStrong value="Ngày tạo: " />
                    <UIFormatDateTimeVNComponent value={data.createdAt} />
                    <br />
                    <Link to={`/tools/tasks/projects/${data.sprint.project.key}/sprint/${data.sprint.key}/toDoList`}>
                        <MyButton
                            value="Chi tiết"
                            type="secondary" />
                    </Link>
                </Box>
            );
        })
    }
    return (
        <Row>
            <Col span={24}>
                <h3>
                    <TextStrong value="Thống kê công việc" />
                </h3>
            </Col>
            <Col span={12}>
                <h3>Công việc chưa làm</h3>
                {RenderToDoListDontWork()}
            </Col>
        </Row>
    )
}