import { Col, Row } from "antd"
import { MyButton } from "../../../core/MyButton";

export default function TaskReportComponent() {
    return (
        <div>
            <Row>
                <Col span={16}>
                    <h4>Thống kê task (2 vùng Vùng 1: Các task sắp hết hạn, các task quán hạn; Vùng 2: Các task đang mở, các task đã đóng)</h4>
                </Col>
                <Col span={8} style={{ textAlign: "right" }}>
                    <MyButton value="Quản lý công việc" />
                </Col>
            </Row>

            123456789
        </div>
    );
}