import { Col, Row } from "antd"
import { Link } from "react-router-dom";
import { Box } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import { Text, TextStrong } from "../../../core/Text";

export default function ProjectReportComponent(props: any) {
    const { projectNew, projectOpen, projectClosed } = props;

    function RenderItem(data: any, index: number) {
        return (
            <Box key={index}>
                <TextStrong value={data.name} />
                <br />
                <Text>Trạng thái: {data.status.value}</Text>
                <Link to={`/tools/tasks/projects/${data.key}`}><MyButton type="secondary" value="Chi tiết" /></Link>
            </Box>
        );
    }

    function RenderProjectNew() {
        return projectNew.map((data: any, index: number) => RenderItem(data, index))
    };


    function RenderProjectOpen() {
        return projectOpen.map((data: any, index: number) => RenderItem(data, index));
    }

    function RenderProjectClosed() {
        return projectClosed.map((data: any, index: number) => RenderItem(data, index));
    }

    return (
        <Row gutter={12}>
            <Col span={16}>
                <h3><TextStrong value="Quản lý dự án" /></h3>
            </Col>
            <Col span={8} style={{ textAlign: "right" }}>
                <Link to="/tools/tasks/projects">
                    <MyButton value="Quản lý dự án" type="success" />
                </Link>
            </Col>
            <Col span={8}>
                <h4>Dự án mới</h4>
                {RenderProjectNew()}
            </Col>
            <Col span={8}>
                <h4>Dự án đang mở</h4>
                {RenderProjectOpen()}
            </Col>
            <Col span={8}>
                <h4>Dự án đã đóng</h4>
                {RenderProjectClosed()}
            </Col>
        </Row>
    );
}