import { Col, Row } from "antd";
import { Link } from "react-router-dom";
import { MyButton } from "../../../core/MyButton";
import { TextStrong } from "../../../core/Text";

export default function WorkflowReportComponent() {
    return (
        <Row>
            <Col span={12}>
                <h3><TextStrong value="Quản lý workflow" /></h3>
            </Col>
            <Col span={12} style={{ textAlign: "right" }}>
                <Link to={`/tools/tasks/workflows`}>
                    <MyButton type="success" value="Quản lý workflow" />
                </Link>
            </Col>
        </Row>
    );
}